import React, { useState, useMemo } from "react";
import { useLocation } from "@reach/router";
import Header from "../../../componets/header/Header";
import Footer from "../../../componets/footer/Footer";
import { events } from "../../../data/events";
import { useLanguage } from "../../../context/LanguageContext";
import { getContent } from "../../../content/content";
import "../../../styles/admission.css";

const AdmissionPage: React.FC = () => {
  const location = useLocation();
  const { lang } = useLanguage();
  const globalContent = getContent(lang);
  const content = globalContent.pages.landing.section.eventContent;

  // Safely extract {event-name} from the URL path and find event data
  const { eventName, eventData } = useMemo(() => {
    if (!location?.pathname) return { eventName: "Unknown Event", eventData: null };
    const parts = location.pathname.split("/");
    const eventSlug = parts[2];
    const foundEvent = events.find(e => e.id === eventSlug);
    return {
      eventName: foundEvent?.name || decodeURIComponent(eventSlug || "Unknown Event"),
      eventData: foundEvent
    };
  }, [location]);

  const [state, setState] = useState<
    "scanner" | "fail" | "used" | "valid" | "success"
  >("scanner");

  const handleAdmit = () => setState("success");

  const renderState = () => {
    switch (state) {
      case "scanner":
        return (
          <>
            <div className="admission-emoji" aria-hidden>
              üì∑
            </div>
            <h3 className="admission-state-title">{content.admission.states.scanner.title}</h3>
            <p className="admission-desc">{content.admission.states.scanner.description}</p>
          </>
        );
      case "fail":
        return (
          <>
            <div className="admission-emoji" aria-hidden>
              ‚ùå
            </div>
            <h3 className="admission-state-title">{content.admission.states.fail.title}</h3>
            <p className="admission-desc">{content.admission.states.fail.description}</p>
          </>
        );
      case "used":
        return (
          <>
            <div className="admission-emoji" aria-hidden>
              ‚ö†Ô∏è
            </div>
            <h3 className="admission-state-title">{content.admission.states.used.title}</h3>
            <p className="admission-desc">{content.admission.states.used.description}</p>
          </>
        );
      case "valid":
        return (
          <>
            <div className="admission-emoji" aria-hidden>
              ‚úÖ
            </div>
            <h3 className="admission-state-title">{content.admission.states.valid.title}</h3>
            <p className="admission-desc">{content.admission.states.valid.description}</p>
            <button className="admit-button" onClick={handleAdmit}>{content.admission.states.valid.button}</button>
          </>
        );
      case "success":
        return (
          <>
            <div className="admission-emoji" aria-hidden>
              üéâ
            </div>
            <h3 className="admission-state-title">{content.admission.states.success.title}</h3>
            <p className="admission-desc">{content.admission.states.success.description}</p>
          </>
        );
      default:
        return null;
    }
  };

  return (
    <>
      <Header content={globalContent?.pages?.globals?.section?.headerContent} />
      <main className="admission-page" role="main">
        <h1 className="admission-title">{content.admission.title} ‚Äì {eventName}</h1>

        <section className="admission-card" data-state={state} aria-live="polite">
          {renderState()}
        </section>

        <div className="debug-buttons" aria-label="Debug state controls">
          <button onClick={() => setState("scanner")}>{content.admission.debug.scanner}</button>
          <button onClick={() => setState("fail")}>{content.admission.debug.failed}</button>
          <button onClick={() => setState("used")}>{content.admission.debug.used}</button>
          <button onClick={() => setState("valid")}>{content.admission.debug.valid}</button>
          <button onClick={() => setState("success")}>{content.admission.debug.success}</button>
        </div>
      </main>
      <Footer content={globalContent?.pages?.globals?.section?.footerContent} />
    </>
  );}


export default AdmissionPage;
